import React from 'react';
import { useState, useEffect } from "react";
import { Col, Container, Row } from 'react-bootstrap';
import { ArrowRightCircle } from 'react-bootstrap-icons';
import myPhoto from '../assets/img/myphoto.png';
import 'animate.css';
import TrackVisibility from 'react-on-screen';


const Banner = () => {
    const [loopNum, setLoopNum] = useState(0);
    const [isDeleting, setIsDeleting] = useState(false);
    //typing and deleting
    const toRotate = ["Web Developer", "Web Designer", "UI/UX Designer"];
    const [text, setText] = useState('');
    const [delta, setDeleta] = useState(300 - Math.random() * 100);
    const period = 2000;

    useEffect(() => {
        let ticker = setInterval(() => {
            tick();
        }, delta)

        return () => {
            clearInterval(ticker)
        };
    }, [text]);

    const tick = () => {
        let i = loopNum % toRotate.length;
        let fullText = toRotate[i]; //=>
        let updatedText = isDeleting ? fullText.substring(0, text.length - 1) : fullText.substring(0, text.length + 1);
        //if(isDeleting = true), (0 - length-1)=>

        setText(updatedText);

        if (!isDeleting) {
            setDeleta(prevDelta => prevDelta / 2)
        }

        if (!isDeleting && updatedText === fullText) { //isDeleting=true update =>typing
            setIsDeleting(true);
            setDeleta(period);
        } else if (isDeleting && updatedText === '') { //typing
            setIsDeleting(false);
            setLoopNum(loopNum + 1);
            setDeleta(500);
        }

    }


    return (
        <section className="banner" id="home">
            <Container>
                <Row className="align-items-center">
                    <Col xs={12} md={7} xl={8}>
                        <TrackVisibility>
                            {({ isVisible }) =>
                                <div className={isVisible ? "animate__animated animate__fadeIn" : ""}>
                                    <h1>
                                        <div style={{ color: '#5C493E', paddingTop: '25px' }}>{`Hi, I'm Selina Su.`}<br /></div>
                                        <span className="wrap" style={{ color: '#F5B85D' }}>{text}</span>
                                        {/* {console.log(text)} */}
                                    </h1>
                                    <p style={{ color: '#5C493E' }}>UIUXRWD</p>
                                    <button style={{ color: '#BF6730' }} onClick={() => console.log('connect')}> <ArrowRightCircle size={25} color="#BF6730" /></button>
                                </div>}
                        </TrackVisibility>
                    </Col>
                    <Col xs={12} md={5} xl={4}>
                            <img src={myPhoto} alt="Header Img" />
                    </Col>
                </Row>
            </Container>
        </section>
    )
}

export default Banner